import React from 'react';
import ReactDOM from 'react-dom';
import { NextScript } from 'next/document';
import Head from 'next/head';

import Box from '../../ui/box';
import InputText from '../../ui/inputText';
import Button from '../../ui/button';

class FingerPrint extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      fingerPrintSubj: '',
    };

    this.supreme = props.supreme;
    this.renderFingerPrintProjector = this.renderFingerPrintProjector.bind(this);
  }
  renderFingerPrintProjector() {
    return (
      <Box flexDirection="row">
        <Box
          width="50%"
          borderStyle="groove"
          align="center"
        >
          <Box
            height="400px"
            borderStyle="groove"
          >
            left thumb
          </Box>
          <Box>
            <button
              type="button"
              onClick={(e) => {
                this.setState({
                  fingerPrintSubj: 'left',
                });
              }}
            >
              Get Left Thumb
            </button>
          </Box>
        </Box>
        <Box
          width="50%"
          borderStyle="groove"
          align="center"
        >
          <Box
            height="400px"
            borderStyle="groove"
          >
            right thumb
          </Box>
          <Box>
            <button
              type="button"
              onClick={(e) => {
                this.setState({
                  fingerPrintSubj: 'right',
                });
              }}
            >
              Get Right Thumb
            </button>
          </Box>
        </Box>
      </Box>
    );
  }
  renderFingerPrintScanner(thumb) {
    console.log(selectChangeEvent);
    return (
      <Box>
        <Box align="right">
          <button
            type="button"
            onClick={(e) => this.setState({ fingerPrintSubj: '' })}
          >
            close
          </button>
        </Box>
        <Box align="center">
          <h5>Scan {thumb} thumb</h5>
        </Box>
        <Box
          align="center"
          height="300px"
          align="center"
          borderStyle="groove"
        >
          <h4>Select Reader :</h4>
            <select className="form-control" id="readersDropDown" onChange={selectChangeEvent}>
            </select> 
          <br />
          <div id="Scores">
            <h5>Scan Quality : <input type="text" id="qualityInputBox" size="20" /></h5> 
          </div>
          <div id="content-capture">    
            <div id="status"></div>            
            <div id="imagediv"></div>
            <div id="contentButtons">
              <input
                type="button"
                className="btn btn-primary"
                id="save"
                value="Save"
                image="rightprint"
              />
            </div>
            <div id="imageGallery">
            </div>
          </div>
        </Box>
      </Box>
    );
  }
  render() {
    return (
      <div>
        <Box>
          <h3>
            Finger Print
          </h3>
          <p>
            Require Finger Print to be taken.
          </p>
        </Box><hr />
        { (this.state.fingerPrintSubj === '') ?
            this.renderFingerPrintProjector()
            :
            this.renderFingerPrintScanner(this.state.fingerPrintSubj)
        }
        <br />
        <Box>
          <Button
            type="button"
            onClick={(e) => {
              this.supreme.switchPageHandler('applicantSignature');
            }}
          >
            Confirm and Go to Next Tab
          </Button>
        </Box>
        <p>this is updated</p>
        <script src="/static/sdk/fingerPrintScanner/lib/jquery.min.js" />
        <script src="/static/sdk/fingerPrintScanner/lib/bootstrap.min.js" />
        <script src="/static/sdk/fingerPrintScanner/scripts/es6-shim.js" />
        <script src="/static/sdk/fingerPrintScanner/scripts/websdk.client.bundle.min.js" />
        <script src="/static/sdk/fingerPrintScanner/scripts/fingerprint.sdk.min.js" />
        <script src="/static/sdk/fingerPrintScanner/app.js" />
      </div>
    );
  }
}

export default FingerPrint;